
















// import i18n from 'i18next';
import { fetchAntaPropertyByCode } from '@/loaders/dictionary';

type Property = {
  id: string,
  value: string| number,
  isDelete?: 0| 1,
};

export default {
  name: 'DictionarySelector',
  props: {
    // 选中的值
    value: {
      type: [String, Number],
      default: '',
    },
    // 字典类型
    type: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      selectedValue: '',
      options: [] as unknown as () => Property[],
      // optionIsDelete: false,
    };
  },
  async created() {
    try {
      this.options = (await fetchAntaPropertyByCode(
        this.type,
      ));
      // this.options = await DictionaryCache.default.getDictionaryByType(this.type);
      // if (this.options.find((o: Property) => o.id === this.value)) {
      //   this.optionIsDelete = true;
      // }
    } catch (err) {
      this.$errHandler(err);
    }
  },
  watch: {
    value: {
      handler(val) {
        this.selectedValue = val;
      },
      immediate: true,
    },
  },
  methods: {
    handleChange() {
      const selectedOptions = this.options.find(item => item.id === this.selectedValue);
      this.$emit('change', selectedOptions);
    },
  },

};
