



























// import i18n from 'i18next';
import {
  defineComponent, ref, computed, watch,
} from '@vue/composition-api';
import axios from '@/utils/axios';
import { fetchAntaPropertyById } from '@/loaders/dictionary';

// import { getCompanyNameById } from '@/api/company';

import { getContextUser } from '@/loaders/context';
import { getCurrentCompany } from '@/loaders/company';

import { antaMiddleClassIdAndFormData2TransformedFormDataList } from '@/components/anta-customer-files/attribute';
import FormFielText from './FromFieldText.vue';


// 获取中类categoryId
async function fetchMiddleCategoryId(id: string) {
  const ids = (await axios({
    method: 'GET',
    url: '/anta-material/category/parents',
    params: { id },
  })).data as string[];
  return ids[1];
}

// 获取大中小类组合的类名
async function getCategoryName(id: string) {
  return ((await axios({
    method: 'post',
    url: '/anta-material/category/name',
    data: {
      ids: [id],
    },
  })).data)[id];
}

// 根据中类id获取模版信息
export const getTemplateById = async (id: string) => {
  return (await axios({
    method: 'get',
    url: 'anta-material/template/middle',
    params: {
      id,
    },
  })).data;
};

// 获取供应商名称
export async function fetchSupplierName(id:string) {
  const res = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: { ids: [id] },
  })).data;
  return res?.[0]?.name || '';
}

interface Unit {
  type: string,
  value: number | string,
}

interface FormField {
  label: string,
  value?: string,
  units?: Array<Unit>,
}

export default defineComponent({
  name: 'MaterialDetailText',
  components: { FormFielText },
  props: {
    material: {
      type: Object,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
  },
  setup(props, { root, emit }) {
    const loading = ref(true);
    const fields = ref<Array<FormField>>([]);
    const attachFields = ref<Array<FormField>>([]);
    const isCreator = computed(() => getContextUser()?.id === props.material.creator);

    const isSupplier = computed(() => {
      return getCurrentCompany()?.type === 3;
    });

    const middleCategoryId = ref<string>('');

    const categoryName = ref<string>('');

    const supplierName = ref<string>('');

    watch(() => props.material, async (val) => {
      console.info(val);
      loading.value = true;

      let colorLimitType = '';
      switch (val.extraProperty.color_limit_type) {
        case 1: colorLimitType = '全部颜色';
          break;
        case 2: colorLimitType = '仅部分颜色';
          break;
        case 3: colorLimitType = '荧光色除外';
          break;
        default:
      }

      const priceItem = val.extraProperty.supplier_price_obj ?? val.extraProperty.supplier_price_array[0];

      fields.value = [
        { label: '分类', value: categoryName.value },
        {
          label: '季节',
          value: `${val.extraProperty.season?.year || ''}${val.extraProperty.season?.season || ''}`,
        },
        { label: '供应商名称', value: priceItem.supplier_name },
        {
          label: '参考单价',
          value: priceItem.reference_price_value ? `${priceItem.reference_price_value}人民币` : '--',
        },
        {
          label: '成本单价',
          value: priceItem.cost_value ? `${priceItem.cost_value}人民币` : '--',
        },
        { label: '颜色限制类别（鞋）', value: colorLimitType },
        { label: '材料编码', value: val.extraProperty.material_number },
      ];

      if (!isSupplier.value) {
        fields.value.push({
          label: '常青材料',
          value: val.extraProperty.evergreen === 1 ? 'TRUE' : 'FALSE',
        });
      }

      // 可能会报错的接口放到后面去请求
      try {
        // 获取分类名
        categoryName.value = await getCategoryName(val.extraProperty.category);
        fields.value[fields.value.findIndex(field => field.label === '分类')].value = categoryName.value;
        // 获取供应商名称
        supplierName.value = await fetchSupplierName(priceItem.supplier_name);
        fields.value[fields.value.findIndex(field => field.label === '供应商名称')].value = supplierName.value;

        // 获取中类ID
        middleCategoryId.value = await fetchMiddleCategoryId(val.extraProperty.category);

        const dynamicFormData = await antaMiddleClassIdAndFormData2TransformedFormDataList(middleCategoryId.value, val.extraProperty);
        console.info(dynamicFormData);
        // const dynamicFormDataKeys = Object.keys(val.extraProperty).filter(key => {
        //   return !['category',
        //     'season',
        //     'evergreen',
        //     'material_number',
        //     'material_status',
        //     'color_limit_type',
        //     'supplier_price_array',
        //     'supplier_price_obj',
        //     'status_modifier',
        //     'decline_reason',
        //     'cite_status',
        //     'approver_id',
        //     'decliner_id',
        //     'deactiver_id',
        //     'approve_time',
        //     'deactive_time',
        //     'uploadFrom',
        //     'decline_time'].includes(key);
        // });
        // console.info(dynamicFormDataKeys);
        fields.value = [...fields.value, ...dynamicFormData];

        const productType = await fetchAntaPropertyById(val.extraProperty.season['品牌']);
        fields.value[fields.value.findIndex(field => field.label === '季节')].value += productType;
        emit('loaded', props.material);
      } catch (e) {
        root.$errorHandler(e);
      } finally {
        loading.value = false;
      }
    }, {
      deep: true,
      // immediate:
    });

    return {
      loading,
      fields,
      attachFields,
      isCreator,
      categoryName,
      supplierName,
      isSupplier,
    };
  },
});
