import {
  JSONSchema7,
} from 'json-schema';

type JSONSchemaType = 'string' | 'number' | 'object' | 'array' | 'integer';

abstract class IJsonschema {
  $schema?: string;
  $id?: string;
  type!: JSONSchemaType;
  title?: string;
  description?: string;
  default?: {
    [k: string]: string
  };
  required?: string[];
  items?: any[];
  properties?: {
    [k: string]: IJsonschema
  };

  oneOf?: IJsonschema[];
  const?: JSONSchemaType;

  // number相关限制
  maximum?: number;
  minimum?: number;
  exclusiveMaximum?: number;
  exclusiveMinimum?: number;

  // array限制
  minItems?: number;

  // string限制
  minLength?: number;
  maxLength?: number;

  // 自定义属性，解析时要进行处理
  /** 表单项将根据这份排序按序生成 */
  sort?: string[];
  /** 标志是否需要自定义解析 */
  custom?: boolean;
  /** 标志自定义解析的类型，常用语自定义解析时使用 */
  customType?: string;
  /** 自定义校验器的名称，表单校验此项时会使用此名称的校验器，校验函数传入JSONschema2Type组件 */
  validator?: string[];
  /** class将传递给对应的组件，用法和vue的class一致 */
  class?: string[] | string;
  /** style将传递给对应的组件，用法和vue的style一致 */
  style?: string | object;
  /** 将传递给对应的组件的上一层form-item，用法和vue的class一致 */
  wrapperClass?: string[] | string;
  /** 将传递给对应的组件的上一层form-item，用法和vue的style一致 */
  wrapperStyle?: string | object;
  /** 是否只读 */
  disabled?: boolean;
  /** 需不需要form-item那一层 */
  notNeedFormItem?: boolean;
  /** 表明对象中哪个是value值。选择类型时，用于传递value对应的key */
  valueKey?: string;
  /** 表明对象中哪个是vlabel值。选择类型时，用于传递label对应的key */
  labelKey?: string;
  /** 输入框类型 */
  inputType?: 'text' | 'password' | 'email' | 'textarea' | 'color' | 'url';
  /** 等价于vue的v-show */
  show?: boolean;
  /** */
  needCalculateItems?: (keyof Pick<IJsonschema, 'show' | 'disabled'>)[];
  /** 自定义属性 */
  [k: string]: unknown;
}

export class Jsonschema extends IJsonschema implements JSONSchema7 {
  constructor(jsonsch: Partial<IJsonschema> & NonNullable<Pick<IJsonschema, 'type'>>) {
    super();
    Object.assign(this, jsonsch);
    return this;
  }
  // setSchemaVersion(target: IJsonschema) {
  //   target.$schema = 'http://json-schema.org/draft-07/schema';
  //   return this;
  // }
}

export default Jsonschema;
