<template>
  <div class="renderer-container" ref="rendererBox">
    <!-- 辅助操作工具栏 -->
    <div class="icons-bar">
      <icon-group
        :model="model"
        :modelNumber="modelNumber"
        @open-instruction-dialog="showDialogInstruction(0)"
        @open-dialog="showShoeInfo"
        :fullscreen="isFullscreen"
        @creating-annotation="annotationMode = 'creation'"
        @update:fullscreen="showFullScreen"
        @forward-operation="handleNextOparation"
        @backward-operation="handlePreviousOperation" />

      <!-- 帧数效果说明 -->
      <p v-if="pfsVisible" class="pfs-text">
        {{$t('model.detail.current_render_speed')}}: {{ fps }}</p>
    </div>

    <!-- 内容栏 -->
    <div class="canvas-wrapper">
      <canvas ref="previewer" @dragover.prevent @drop.prevent.stop v-on="canvasEvent" />
      <!-- <RendererD4 :modelData="modelData"/> -->

      <!-- 加载进度条 -->
      <div class="loading" v-if="loadingStatus">
        <div class="content">
          <div class="progress">
            <span :style="`width: ${loadingStatus.percentage}%`" />
          </div>
          <div v-html="$t('model.detail.rendering_model_percentage', {
              loadingStatus,
            })">
            <!-- <span class="percentage">{{loadingStatus.percentage}}%</span> -->
          </div>
        </div>
      </div>

      <!-- 创建批注弹框 -->
      <!--@delete="(ann) => removeAnnotation(ann.id)"-->
      <AnnotationCreation
        v-if="signCurrent"
        :key="signCurrent.id"
        class="annotation-creation-box"
        ref="annotationCreation"
        :sign="signCurrent"
        :readonly="annotationMode === 'preview'"
        @viewImages="viewImages"
        @close="unfocusAnnotation()"
        @delete="$act(removeAnnotation, [signCurrent])"
        @submit="(...args) => $act(handleAnnotationCreation, args)" />

    </div>

    <!-- 选择部件 -->
    <div class="part-selected" v-if="mode === 'annotation'">
      {{ $t('model.detail.annotation.double_click_to_add_sign') }}
    </div>
    <div v-else class="part-selected" @select="()=>false" @selectstart="() => false">
      {{ $parent.annotationStatus ? $t('model.detail.select_component_tip') :
        partSelectedName ||  $t('model.detail.select_component_tip') }}
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import screenfull from 'screenfull';
import { debounce } from 'throttle-debounce';
import Vue from 'vue';

import ModelInfo from '@/views/model/info';
import handBagInfo from '@/views/model/bag-info/index.vue';
import draggingData from '@/utils/dragging-data';
import { getContextUser } from '@/loaders/context';
import batchBinder from '_/batch-binder';

import {
  getModelCTMList,
  getGoodMaterialList,
  saveShoe,
  saveAsShoe,
  addAnnotation,
  getAnnotationList,
  deleteAnnotation,
  updateAnnotation,
} from '@/api/shoe';
import uploadBase64ToOSS from '@/utils/oss/uploadBase64ToOSS';
import { loadD4, loadD2 } from '@/utils/load-d4';
import progressTween from '@/utils/progress-tween';
import {
  // getWorkpaceCompnayInfo,
  getWorkspaceConfigSelf,
} from '@/api/permission';
import { getItem } from '@/utils/oss/utils';
import { normalizeComponent, normalizeRendererArguments } from '@/utils/normalize-component';
import { dataUrl2Blob } from '_/blob-utils';
import { OSSUploader } from '@/utils/oss/uploader';
import iconBase64 from '@/assets/img/cursor/location.png';
// import { getMaterialDetail } from '@/api/material-mall';
import MaterialStatus from '@/models/MaterialStatus';
import AnnotationCreation from '../annotation/AnnotationCreation';
import axios from '../../../../api/axios';
import Instruction from './Instruction';
import IconGroup from './IconGroup';
import eventBus from './eventBus';

// import RendererD4 from './RendererD4.vue';

// eslint-disable-next-line

async function getDeleteStatus(materialIds) {
  return (await axios.post('bff-center/cloud-disk/list/getCatalogIdStatusByRelateIds', materialIds)).data;
}

async function getEnvSetting(shoeId, relateType) {
  return (
    await axios.get('/show/renderTask/envsetting', {
      params: {
        shoeId,
        relateType,
      },
    })
  ).data;
}

export default Vue.extend({
  name: 'Renderer',
  props: {
    role: String,
    currentVersion: Object,
    model: {},
    workspaceId: {
      default() {
        return this.$route.query.from === '0' ? null : this.$route.query.from;
      },
    },
    modelId: {
      default() {
        return this.$route.params.modelId;
      },
    },
    catalogId: {
      default() {
        return this.$route.query.catalogId;
      },
    },
    relateType: {
      default() {
        return this.$route.query.relateType;
      },
    },
  },
  errorInjector: {
    catchHook: 'mounted',
  },
  components: {
    IconGroup,
    AnnotationCreation,
    // RendererD4,
  },
  data() {
    this.d2 = null;
    this.d4 = null;
    return {
      canvasEvent: {},
      isFullscreen: screenfull.isFullscreen,
      saveButtonStatus: true,
      modelNumber: '',
      loadingStatus: undefined,
      partSelected: null, // 选中的部件
      pfsVisible: false, // 帧数文字显示状态
      editStatus: false, // 排料框显示状态
      dialogShoeInfoTitle: 'Design Info',
      dialogShoeInfoMode: 'preview',
      modefiedRecordLength: 0, // 临时变量
      ctmList: [], // 保存ctm列表,用于部件拆分改造
      materialsCache: {}, // 面料缓存，用于撤销同步当前面料
      /* 批注相关 */
      versions: [],
      annotations: [],
      annotationMode: 'preview', // Mode: preview, creation
      signCurrent: null,
      // modelData: null,
    };
  },
  computed: {
    /** @return { any } */
    modelDetail() {
      return this.$closest('model');
    },
    /** @return { any } */
    mode() {
      return this.modelDetail.annotationStatus ? 'annotation' : 'material';
    },
    partSelectedName() {
      return this.partSelected?.name || '';
    },
    /** @returns { object[] } */
    currentMaterials() {
      return Object.values(Object.fromEntries(
        this.ctmList.filter(item => item.material).map(item => {
          const { material } = item;
          return [material.id, material];
        }),
      ));
    },
  },
  watch: {
    // 切换模式时，隐藏或显示批注
    mode: {
      async handler(value) {
        if (value) { this.annotationInfoBoxVisible = false; }
        if (this.d4) this.renderAnnotationsInModel();
        await this.$nextTick();
        this.$refs.previewer.style.cursor = value === 'annotation'
          ? `url(${iconBase64}) 3 18, crosshair` : 'auto';
      },
      immediate: true,
    },
  },
  async mounted() {
    this._uninit = await this.init();
    this.renderAnnotationsInModel();
  },
  // 关闭页面时，停止渲染鞋模型
  destroyed() {
    if (this._uninit) {
      this._uninit();
    }
  },
  methods: {
    viewImages(...args) {
      this.$ImageViewer.show(...args);
    },
    async fetchVersions() {
      this.versions = (await getAnnotationList(
        this.catalogId, undefined, this.workspaceId,
      )).data;

      this.annotations = this.versions
        .find(version => version.relateId === this.modelId)
        .tdAnnotationResponseList;
    },
    // 渲染批注列表到鞋模型
    async renderAnnotationsInModel() {
      if (this.mode === 'annotation') {
        this.annotations.forEach(({ id }) => {
          try {
            this.d4.deleteSign(id);
          } catch (e) { }
        });
        await this.fetchVersions();
        for (let idx = 0; idx < this.annotations.length; idx++) {
          const ann = this.annotations[idx];
          await this.d4.addSign(
            ann.threeDimensionalInfo,
            this.d2.getSignTexture(this.annotations.length - idx),
          );
        }
        this.d4.renderSigns();
      } else {
        this.unfocusAnnotation(this.signCurrent);
        this.d4.hideSigns();
      }
    },
    // 创建新批注
    async handleAnnotationCreation(content) {
      const modifierInfo = getContextUser();
      const accessoryInfo = { ...content.accessoryInfo } ?? {
        images: [],
      };
      accessoryInfo.images = [...accessoryInfo.images].map(url => {
        return url.split('?')[0].split('/').slice(-2).join('/');
      });
      if (content.id) {
        await updateAnnotation({
          id: content.id,
          shoeId: this.model.id,
          content: content.content,
          threeDimensionalInfo: { ...content.threeDimensionalInfo, id: undefined },
          accessoryInfo,
          brushJson: JSON.parse(content.brushJson),
          catalogId: this.catalogId,
          modelNumber: this.model.modelNumber,
        }, this.workspaceId);
        content = {
          ...this.signCurrent,
          ...content,
          modifierInfo,
          gmtModifyTime: Date.now(),
        };
      } else {
        const id = (await addAnnotation({
          shoeId: this.model.id,
          content: content.content,
          threeDimensionalInfo: { ...content.threeDimensionalInfo, id: undefined },
          accessoryInfo,
          brushJson: JSON.parse(content.brushJson),
          catalogId: this.catalogId,
          modelNumber: this.model.modelNumber,
        }, this.workspaceId)).data;
        this.d4.updateSignId('new_sign', id);
        content = {
          ...this.signCurrent,
          ...content,
          id,
          modifierInfo,
          gmtModifyTime: Date.now(),
        };
      }

      this.signCurrent = content;
      this.$emit('add-annotation', this.signCurrent);
      await this.fetchVersions();
      this.annotationMode = 'preview';
    },
    unfocusAnnotation() {
      if (this.signCurrent) {
        if (this.signCurrent.threeDimensionalInfo.id === 'new_sign') {
          this.d4.deleteSign(this.signCurrent.threeDimensionalInfo.id);
        }
        this.signCurrent = null;
      }
    },
    async removeAnnotation(ann) {
      this.d4.deleteSign(ann.id);
      if (this.signCurrent.id === ann.id) {
        this.signCurrent = null;
      }
      await deleteAnnotation(ann.id, this.workspaceId, ann.shoeId, this.catalogId, this.model.modelNumber);
      const vmAnnotation = this.modelDetail.annotation;
      if (vmAnnotation) {
        const version = vmAnnotation.versions.find((v) => v.relateId === ann.shoeId);
        if (version) {
          const annotations = version.tdAnnotationResponseList;
          const idx = annotations.findIndex(annotation => annotation.id === ann.id);
          if (idx > -1) {
            annotations.splice(idx, 1);
          }
        }
      }
    },
    async showAnnotation(ann, readonly) {
      ann.position = this.d4.getSignScreenPos(ann.id);
      this.signCurrent = ann;
      this.annotationMode = readonly ? 'preview' : 'creation';
      if (!readonly) {
        // eslint-disable-next-line no-unused-expressions
        this.$refs?.annotationCreation?.$refs?.textarea?.focus();
      }
    },
    async getMaterialById(materialId) {
      return (await axios.get('/bff-center/showCommon/getMaterialBasicInfoAndTags', {
        params: {
          materialId,
          language: i18n.language,
        },
      })).data;
    },
    handlePreviousOperation() {
      this.d4.backwardAlter();
      this.syncCtmList();
      if (this.d2) {
        this.d2.reset2D(this.d4);
      }
    },
    handleNextOparation() {
      this.d4.forwardAlter();
      this.syncCtmList();
      if (this.d2) {
        this.d2.reset2D(this.d4);
      }
    },
    // 撤销同步ctmList
    syncCtmList() {
      const ctmData = this.d4.outputDatas();
      this.ctmList.forEach(async (_ctm) => {
        const comp = ctmData.find(item => item.componentID === _ctm.ctmId);
        if (comp.materialID === _ctm.materialId) return;
        let material = this.materialsCache[comp.materialID];
        if (!material) {
          // 获取material
          material = await this.getMaterialById(comp.materialID);
          this.materialsCache[material.id] = material;
        }
        _ctm.materialId = material.id;
        _ctm.material = material;
      });
    },
    bindD4() {
      const { d4 } = this;
      const getTarget = (event) => {
        const canvasDom = this.$refs.previewer;
        const param = {
          left: canvasDom.getBoundingClientRect().left,
          top: canvasDom.getBoundingClientRect().top,
          clickX: event.clientX,
          clickY: event.clientY,
          eventType: event.type,
        };
        const result = d4.getTargetFromScreen(param);
        // refresh2D();
        // 处理放回是null的情况
        return result || { id: '', name: '' };
      };

      this.canvasEvent = {
        click: async (e) => {
          const d4Sign = d4.getSign(e.offsetX, e.offsetY);

          if (d4Sign) {
            const ann = this.annotations.find(_ann => _ann.id === d4Sign.id);
            if (ann) {
              this.showAnnotation(ann, true);
            }
          }
        },
        dblclick: async (e) => {
          // 在不同模式（拖拽面料和批注模式）下响应不同的事件处理
          if (!this.modelDetail.annotationStatus) {
            this.partSelected = getTarget(e);
          } else if (this.modelDetail.annotationStatus) {
            this.annotationMode = 'creation';
            const d4Sign = d4.createSign(
              e.offsetX,
              e.offsetY,
              'new_sign',
              this.d2.getSignTexture(this.annotations.length + 1),
            );

            await this.$nextTick();
            this.signCurrent = {
              shoeId: this.model.id,
              content: '',
              modifierInfo: getContextUser(),
              accessoryInfo: {
                images: [],
              },
              threeDimensionalInfo: d4Sign,
              position: { x: e.offsetX, y: e.offsetY },
            };
          }
        },
        mousedown: (event) => {
          const controlsMouseMove = (e) => {
            this.unfocusAnnotation();
            d4.controlsMouseMove(e);
            d4.mouseMoveRender(e);
          };

          function controlsMouseUp() {
            d4.controlsMouseUp();
            d4.render();
            document.removeEventListener('mousemove', controlsMouseMove, false);
            document.removeEventListener('mouseup', controlsMouseUp, false);
          }

          const isStart = d4.controlsMouseDown(event);

          if (isStart) {
            document.addEventListener('mousemove', controlsMouseMove, false);
            document.addEventListener('mouseup', controlsMouseUp, false);
          }
        },
        wheel: (event) => {
          this.unfocusAnnotation();
          event.preventDefault();
          event.stopPropagation();

          d4.controlsMouseWheel(event);
          d4.render();
        },
        drop: (e) => {
          const part = getTarget(e);
          let dropData;
          if (draggingData.type === 'catalog') {
            dropData = draggingData.datum.fileInfo;
          } else if (draggingData.type === 'material') {
            dropData = draggingData.datum;
          }
          if (dropData) {
            this.updateMaterial(dropData, part);
            draggingData.clear();
          }
        },
        touchstart: () => {
          const controlsTouchMove = (e) => {
            this.unfocusAnnotation();
            d4.controlsTouchMove(e);
          };

          function controlsTouchEnd(e) {
            d4.controlsTouchEnd(e);

            document.removeEventListener('touchmove', controlsTouchMove, false);
            document.removeEventListener('touchend', controlsTouchEnd, false);
          }

          document.addEventListener('touchmove', controlsTouchMove, false);
          document.addEventListener('touchend', controlsTouchEnd, false);
        },

      };
    },
    async init() {
      this.loadingStatus = {
        percentage: 0,
      };
      const detectFullScreen = () => {
        this.isFullscreen = screenfull.isFullscreen;
      };
      screenfull.on('change', detectFullScreen);
      try {
        await this.initD4D2();
        this.bindD4();
      } catch (e) {
        this.loadingStatus = null;
        throw e;
      }

      const _batchBinder = batchBinder([
        {
          target: document.body,
          eventType: 'keydown',
          handler: async e => {
            if (document.body !== document.activeElement) return;
            if (!this.editStatus) return;
            this.d2.shortcutKeys(e);
          },
        },
      ]);

      return () => {
        screenfull.off('change', detectFullScreen);
        _batchBinder();
        // this.d4 = null;
      };
    },
    async initD4D2() {
      const getModelData = async (withGoodsData = false) => {
        const ctms = await Promise.all(
          ((await getModelCTMList(this.modelId, this.workspaceId)).data ?? [])
            .map(item => normalizeComponent(item)),
        );
        ctms.forEach(ctm => {
          if (ctm.materialId === 'basic' || ctm.materialId === '1') {
            ctm.materialId = null;
            ctm.material = null;
          }
        });
        const materialIds = Array.from(new Set(ctms.map(item => item.materialId).filter(id => id)));
        const status = await getDeleteStatus(materialIds);
        // const status = {};
        const goodsList = withGoodsData ? (await getGoodMaterialList(materialIds)).data : {};
        const materials = {};
        ctms.forEach((item) => {
          if (!item.material) {
            return;
          }
          let material = materials[item.material.id];
          if (!material) {
            ({ material } = item);
            material.deleteStatus = status[material.id];
            // eslint-disable-next-line camelcase
            material.status = material.extraProperty?.material_status ?? material.materialStatus;
            material.goods = goodsList[material.id];
            material.flat = material.imgJson.flat;
            material.thumbnail = material.imgJson.thumbnail;
            material.currentDesign = true;
            material.materialId = material.id;
            if (item.goods) {
              material.goodId = item.goods.id;
              material.publishStatus = item.goods.publishStatus;
            }
            material.ctms = [];
            // 避免转换json循环引用
            material.toJSON = () => {
              // eslint-disable-next-line @typescript-eslint/no-unused-vars
              const { ctms: _ctms, ..._material } = material;
              return _material;
            };
            materials[material.id] = material;
          }
          material.ctms.push(item);
          item.material = material;
          item.materialId = material.id;
        });
        console.log(materials);
        return ctms;
      };

      const [data, D4, D2, envSetting] = await Promise.all([
        getModelData(true),
        loadD4(),
        loadD2(),
        getEnvSetting(this.modelId, 0),
      ]);

      this.ctmList = data;

      data.forEach(ctm => {
        this.materialsCache[ctm.materialId] = ctm.material;
      });

      if (!data) {
        throw new this.$Error(i18n.t('model.detail.load_component_error'));
      }

      if (!data.length) {
        throw new this.$Error(i18n.t('model.detail.client_not_support'));
      }

      const canvasDom = this.$refs.previewer;

      canvasDom.width = canvasDom.parentElement.clientWidth;
      canvasDom.height = canvasDom.parentElement.clientHeight;

      const d4 = new D4({
        canvas: canvasDom,
        width: canvasDom.parentElement.clientWidth,
        height: canvasDom.parentElement.clientHeight,
        resolution: window.devicePixelRatio,
        envSetting,
      });
      const d2 = new D2();

      const onProgress = progressTween((loadingStatus) => {
        this.loadingStatus = {
          ...loadingStatus,
          percentage: parseInt(loadingStatus.percentage, 10),
        };
        if (loadingStatus.percentage === 100) {
          eventBus.$emit('renderer-loaded');
          this.loadingStatus = undefined;
        }
      }, 3 / 1000, 30, {
        percentage: 0,
      }, 30 / 1000);


      const loadModel = async () => {
        await d4.loadModel(data, (loadingStatus_) => {
          onProgress({
            ...loadingStatus_,
            percentage: loadingStatus_.loaded / loadingStatus_.total * 100,
          });
        });
        onProgress({ percentage: 100 });
      };

      const makeWaterMask = async () => {
        // 水印功能（workspace权限相关）

        const openWatermark = this.workspaceId ? !!(await getWorkspaceConfigSelf(this.workspaceId)).data.openWatermark : false;

        if (this.workspaceId
          && (this.$can(this.workspaceId, 47) || openWatermark)) {
          // const res = await getWorkpaceCompnayInfo(this.workspaceId);
          const { mail, account } = getContextUser();
          await d4.updateWatermask(d2.getWatermask(mail || account));
        }
      };
      await Promise.all([loadModel(), makeWaterMask()]);
      this.d4 = d4;
      this.d2 = d2;
    },
    handleUpdateModelInfo(obj) {
      if (this.saveButtonStatus) this.modelNumber = obj.modelNumber;
    },
    showFullScreen() {
      if (!screenfull.enabled) {
        this.$message.warning(i18n.t('model.detail.browser_not_support_tip'));
      } else {
        screenfull.toggle(this.$refs.rendererBox);
      }
    },
    resize: debounce(500, function resize() {
      if (this.d4) {
        const canvasDom = this.$refs.previewer;

        const params = {
          canvas: canvasDom,
          width: canvasDom.parentElement.clientWidth,
          height: canvasDom.parentElement.clientHeight,
          resolution: window.devicePixelRatio,
        };
        canvasDom.width = params.width;
        canvasDom.height = params.height;
        if (this.$refs.annotationCreation) {
          this.$refs.annotationCreation.resize();
        }
        this.d4.resizeRenderer(params.width, params.height, params.resolution);
      }
    }),

    async showShoeInfo() {
      if (this.relateType === '3') {
        const dialogResult = await this.$Dialog.dialogify({
          is: handBagInfo,
          props: {
            modelId: this.modelId,
            groupAppId: this.workspaceId,
          },
        });
        this.handleUpdateModelInfo(dialogResult);
      } else {
        const dialogResult = await this.$Dialog.dialogify({
          is: ModelInfo,
          props: {
            modelId: this.modelId,
            groupAppId: this.workspaceId,
          },
        });
        this.handleUpdateModelInfo(dialogResult);
      }
    },
    async save(type, modelNumber) {
      // 格式化要上传的数据
      this.d4.hideSigns();
      const shoeID = this.modelId;

      const modelCtmRequestList = await this._formatShoeComponents(
        this.d4.outputDatas(),
      );

      // 上传截图
      let curtailImg = '';
      const uploader = new OSSUploader({ sourceType: 'SHOE', sendAs: 'arrayBuffer' });
      try {
        const isAnnotation = this.mode === 'annotation';
        if (isAnnotation) {
          this.d4.hideSigns();
        }
        const file = dataUrl2Blob(this.d4.getModelSnapshot(512, 512));
        if (isAnnotation) {
          this.d4.renderSigns();
        }
        const { key, path } = await getItem(file, { sourceType: 'SHOE' });
        curtailImg = path;
        // 上传到oss
        await uploader.send({
          params: { key },
          body: file,
          headers: {
            'Content-Type': 'image/png',
          },
        });
      } catch (err) {
        this.$errHandler(err);
      }

      const data = {
        modelId: shoeID,
        modelCtmRequestList,
        curtailImg,
        catalogId: this.catalogId,
      };

      // 根据不同的保存类型，发送不同的请求
      if (type === 'save') {
        const modifiedRecord = this.d4.getRecord();
        data.modifyContent = JSON.stringify(modifiedRecord);
        this.d4.resetOriginRecord(); // 清空修改记录

        // 如果没有进行修改，提示不能保存
        if (!modifiedRecord.length) {
          throw new this.$Error(i18n.t('model.detail.cannot_save_tip'), { level: -1 });
        }

        const newRelateId = (await saveShoe(data, this.workspaceId)).data;
        this.modelId = newRelateId;
        this.saveButtonStatus = true;

        // 替换路由
        await this.$router.replace({
          name: 'ModelItem',
          params: { modelId: newRelateId },
          query: { from: this.workspaceId, catalogId: this.catalogId },
        });

        this.modefiedRecordLength = modifiedRecord.length;
        this.$message.success(i18n.t('model.renderer.saved_suc'));
      }

      if (type === 'save as') {
        Object.assign(data, { newModelName: modelNumber });
        const { newRelateId, newCatalogId } = (await saveAsShoe(data, this.workspaceId)).data;

        // 替换路由
        this.$router.replace({
          name: 'ModelItem',
          params: { modelId: newRelateId },
          query: { from: this.workspaceId, catalogId: newCatalogId },
        });

        // this.modelId = res.data; // 另存为不需要修改modelId
        this.saveButtonStatus = false;
        // this.showShoeInfo();
        this.$message.success(i18n.t('model.renderer.saved_as_suc'));
      }
    },
    async updateMaterial(material, part = this.partSelected) {
      try {
        if (!part) return;
        const ctm = this.ctmList.find(_ctm => {
          return _ctm.componentID === part.id;
        });
        if (!ctm) return;
        const id = material.id ? material.id : material.originalId;
        const s = (await axios.get('/bff-center/showCommon/getMaterialBasicInfoAndTags', {
          params: {
            materialId: id,
            language: i18n.language,
          },
        })).data;
        Object.assign(material, s, s.imgJson, {
          status: (s.extraProperty ?? {}).material_status,
        });

        if (material.goodId) {
          material.goods = (await getGoodMaterialList([material.id])).data[material.id];
          material.publishStatus = material.goods.publishStatus;
        }
        material.deleteStatus = (await getDeleteStatus([material.id]))[material.id];

        if (material.deleteStatus) {
          throw new this.$Error(i18n.t('model.detail.board.material_deleted'));
        } if (material.status === MaterialStatus.REJECTED) {
          throw new this.$Error(i18n.t('model.detail.board.material_rejected'));
        } if (material.status === MaterialStatus.INACTIVE) {
          throw new this.$Error(i18n.t('model.detail.board.material_inactived'));
        } if (material.status === MaterialStatus.DEVELOPING) {
          throw new this.$Error(i18n.t('model.detail.board.material_developing'));
        } if (material.publishStatus === 3) {
          throw new this.$Error(i18n.t('model.detail.board.material_unpublished'));
        }
        if (!material.fileInfo) {
          throw new this.$Error(i18n.t('model.detail.board.no_4ddat'));
        }
        ctm.material = material;
        this.materialsCache[material.id] = material;
        const d4Material = {
          materialID: material.id || material.originalId,
          name: material.name || material.materialName,
          renderArguments: await normalizeRendererArguments(material.renderingJson),
        };
        await this.d4.beforeChangeMaterial(d4Material);
        if (this.d2) {
          this.d2.reset2D(this.d4);
        }
      } catch (err) {
        this.$errHandler(err);
      }
    },
    /* 服装线需求 */
    async updateMaterialByPartId(partId, material) {
      const d4Material = {
        materialID: material.id,
        name: material,
        renderArguments: await normalizeRendererArguments(material.renderingJson),
      };
      await this.d4.beforeChangeMaterial(d4Material, partId);
      if (this.d2) {
        this.d2.reset2D(this.d4);
      }
    },
    async deleteMaterial(material) {
      try {
        const defaultMaterial = (await axios.get('/bff-center/showCommon/getMaterialBasicInfoAndTags', {
          params: {
            materialId: '1',
            language: i18n.language,
          },
        })).data;
        this.ctmList.forEach(_ctm => {
          // 将被删除面料对应部分切换为原始面料
          if (_ctm.materialId === material.id) {
            _ctm.materialId = '1';
            _ctm.material = defaultMaterial;
            this.updateMaterialByPartId(_ctm.componentID, defaultMaterial);
          }
        });
      } catch (err) {
        this.$errHandler(err);
      }
    },
    async replaceMaterial(oldMaterial, newMaterial) {
      try {
        newMaterial = (await axios.get('/bff-center/showCommon/getMaterialBasicInfoAndTags', {
          params: {
            materialId: newMaterial.id,
            language: i18n.language,
          },
        })).data;
        this.ctmList.forEach(_ctm => {
          if (_ctm.materialId === oldMaterial.id) {
            _ctm.materialId = newMaterial.id;
            _ctm.material = newMaterial;
            // this.updateMaterialByPartId(_ctm.componentID, newMaterial);
          }
        });
      } catch (err) {
        this.$errHandler(err);
      }
    },
    handleShowDialogShoeInfo() {
      this.dialogShoeInfoTitle = 'Design Info';
      this.dialogShoeInfoMode = 'preview';
      this.showShoeInfo();
    },
    // 显示操作指南弹框: 根据不同 i 的值，显示当前默认展示页面
    async showDialogInstruction(index) {
      await this.$Dialog.dialogify({
        is: Instruction,
        props: {
          index,
        },
      });
    },
    // 格式化从D4中获取的鞋模型实时部件的数据，转为提交api的格式
    async _formatShoeComponents(components) {
      // return console.info(components.map(it => it.mapSec));
      const result = [];
      // 设置promise数组
      const promiseArr = components.map(async item => {
        let { mapSec } = item;
        if (mapSec) {
          // 判断是否base64
          if (/^\s*data:([a-z]+\/[a-z0-9-+.]+(;[a-z-]+=[a-z0-9-]+)?)?(;base64)?,([a-z0-9!$&',()*+;=\-._~:@/?%\s]*?)\s*$/i.test(mapSec)) {
            mapSec = await uploadBase64ToOSS(mapSec, 'MATERIALPRINTING');
          } else {
            [mapSec] = mapSec.split('?')[0].split('/').reverse();
          }
        }
        let { edgeArrPath } = this.ctmList.find(octm => item.componentID === octm.ctmId);
        [edgeArrPath] = edgeArrPath.split('?')[0].split('/').reverse();
        const originCtm = this.ctmList.find(octm => item.componentID === octm.ctmId) || {};
        if (originCtm.edgeArr) {
          // 将edgeArr上传到OSS获取edgeArrPath
          const uploader = new OSSUploader({ sourceType: 'COMPONENT' });
          try {
            // 上传到oss
            edgeArrPath = (await uploader.send({
              body: new Blob([originCtm.edgeArr], { type: 'application/json' }),
            })).path;
            edgeArrPath = `${edgeArrPath}.json`;
            console.info(edgeArrPath);
          } catch (err) {
            this.$errHandler(err);
          }
        }

        result.push({
          ctmId: item.componentID,
          materialId: item.materialID,
          mapSec,
          edgeArrPath,
          uvTrans: item.uvTrans,
          additionalJson: item.additionalJson,
        });
      });
      await Promise.all(promiseArr);
      return result;
    },
  },
});
</script>

