<template lang="html">
  <div>
    <figure class="material-item"
      :class="{
        selected: selected,
        copying: isCopying,
        moving: isMoving
      }"
      tabindex="0"
      @click.stop="pick"
      @contextmenu.stop="handleContextmenu"
      @dragstart="handleDragStartMaterial(material, $event)"
      @dragover.prevent
      :draggable="!unavailableResaon">

      <BaseImage :lazy="true"
        data-img-type='material-list'
        :data-src="imgSrc"
        :style="{
            width: '96px',
            height: '96px',
          }"
        :src="imgSrc" />
      <!-- <figcaption class="material-item-name">{{ material.name }}</figcaption> -->

      <!-- 小功能按钮 -->
      <i class="iconfont icon-help"
        @click.stop="$emit('show-info', material)" />
      <i
        class="iconfont icon-tick-circle"
        v-if="selectable"
        @click.stop="$emit('update:selected', !selected)"
      />
      <i class="iconfont icon-zoom"
        @click.stop="$emit('magnify', displayMode)" />
      <span v-if="unavailableResaon" class="material-item-publishstatus">
        {{unavailableResaon}}
      </span>
      <span class="material-item-price"></span>
    </figure>
    <el-tooltip class="item" effect="dark" placement="bottom">
      <span slot="content" class="text-one-line">{{ material.name }}</span>
      <span class="material-item-name text-one-line">
        {{ material.name }}
      </span>
    </el-tooltip>
  </div>
</template>

<script>

import BaseImage from '@/components/base/Image';
import clipboard from '@/utils/clipboard';

import i18n from 'i18next';
import MaterialStatus from '@/models/MaterialStatus';

export default {
  components: {
    BaseImage,
  },
  props: {
    selected: {
      type: Boolean,
    },
    material: {
      type: Object,
      required: true,
    },
    // 面料的现实模式 flat/drape
    displayMode: {
      type: String,
      default: 'drape',
    },
  },
  computed: {
    selectable() {
      return this.$listeners['update:selected'];
    },
    imgSrc() {
      return this.displayMode === 'drape'
        ? this.material.thumbnail : this.material.flat;
    },
    inClipboard() {
      return clipboard.type === 'catalog'
        && clipboard.dataArr.some(catalog => catalog.fileInfo === this.material);
    },
    isCopying() {
      return clipboard.command === 'copy'
        && this.inClipboard;
    },
    isMoving() {
      return clipboard.command === 'move'
        && this.inClipboard;
    },
    unavailableResaon() {
      const { material } = this;
      if (material.deleteStatus) {
        return i18n.t('model.detail.board.deleted');
      } if (material.status === MaterialStatus.INACTIVE) {
        return i18n.t('model.detail.board.inactive');
      } if (material.publishStatus === MaterialStatus.REJECTED) {
        return i18n.t('model.detail.board.rejected');
      } if (material.publishStatus === MaterialStatus.REJECTED) {
        return i18n.t('model.detail.board.rejected');
      } if (material.publishStatus === MaterialStatus.DEVELOPING) {
        return i18n.t('model.detail.board.developing');
      } if (material.publishStatus === 3) {
        return i18n.t('model.detail.board.unpublished');
      }
      return null;
    },
  },
  methods: {
    handleContextmenu(e) {
      if (this.$listeners.contextmenu) {
        e.preventDefault();
        this.$emit('contextmenu', e, this.material);
      }
    },
    handleDragStartMaterial(data, e) {
      this.$emit('drag', data, e);
    },
    pick() {
      this.$emit('pick', this.material);
    },
  },
};
</script>

<style lang="scss" scoped>
@keyframes blink {
  50% {
    border-color: $color-primary;
  }
}

