  border-radius: 6px;
  &:hover {
    filter: drop-shadow(0 0 5px rgba(136, 149, 172, 0.4));
  }
  &.selected {
    border-color: $color-primary;
    .icon-tick-circle {
      display: block;
      color: $color-primary;
    }
  }

  &.copying,
  &.moving {
    border-style: dashed;
    animation: blink 2s infinite;
  }
  &.moving {
    > * {
      opacity: 0.5;
    }
  }
  &:hover .iconfont,
  &:hover .material-item-price,
  &:hover .icon-zoom {
    display: block;
  }

  .iconfont,
  .material-item-price {
    display: none;
