    z-index: 2;
    color: #848b97;
  }

  .material-item-publishstatus {
    height: 20px;
    line-height: 20px;
    user-select: none;
    background-color: #838b97;
    border-radius: 10px;
    color: #fff;
    position: absolute;
    top: 40px;
    width: 52px;
    left: 22px;
    text-align: center;
    font-size: 12px;
  }

  .icon-help {
    left: 6px;
    top: 6px;
  }
  .icon-help:hover {
    color: #2878ff;
  }
  .icon-tick-circle {
    right: 6px;
    top: 6px;
  }
  .icon-zoom {
    display: none;
    text-align: center;
    line-height: 18px;
    right: 6px;
    bottom: 6px;
    width: 18px;
    height: 18px;
    background-color: rgba(132, 139, 151, 0.4);
    border-radius: 2px;
    color: #fff;
    &:hover {
      background-color: rgba(0, 0, 0, 0.6);
    }
  }
  .material-item-price {
    right: 4px;
