import { defineComponent, ref } from '@vue/composition-api';

const stringType = defineComponent({
  props: {
    builder: {
      type: String,
      default: 'el-input',
    },
    val: {
      type: Number,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  model: {
    prop: 'val',
    event: 'input',
  },
  render() {
    const s = this as InstanceType<typeof stringType>;
    const { builder } = s;
    return <builder {...{ attrs: s.$attrs }} value={String(s.val || '')}
    oninput={s.handleUpdateValue} disabled={s.disabled} />;
  },
  setup(_, { emit }) {
    function handleUpdateValue(newString: string) {
      if (newString === '') emit('input', null);
      if (!globalThis.isNaN(+newString[newString.length - 1])) emit('input', +newString);
    }
    return {
      handleUpdateValue,
    };
  },
});

export default stringType;
