
const _resizeDispatcher = [
  { eventTypes: ['resize'], target: global },
  { target: global.document, eventTypes: ['webkitfullscreenchange', 'mozfullscreenchange'] },
];

export function resizeWatch(fn, context) {
  const handler = fn.bind(context ?? this);
  _resizeDispatcher.forEach((item) => {
    const { target, eventTypes } = item;
    eventTypes.forEach(t => {
      target.addEventListener(t, handler, false);
    });
  });
  return () => {
    _resizeDispatcher.forEach((item) => {
      const { target, eventTypes } = item;
      eventTypes.forEach(t => {
        target.removeEventListener(t, handler, false);
      });
    });
  };
}
export default resizeWatch;
