import Vue from 'vue';
import tryParseJson from '_/try-parse-json';
import DataCollector from './data-collector';

const storageKey = 'dragging-data';

type Access = () => { command: string, data: any, type: string } | null;
type Mutate = ({ command, data }: {command: string, data: any}) => void;
type Clear = () => void;

function parseData(access: Access, mutatate: Mutate, clear: Clear) {
  const parsed = access();
  let rawData = null;
  let command: string = '';
  let type = 'unknown';
  if (parsed) {
    ({ command, data: rawData, type } = parsed);
  }

  const data = rawData ? new DataCollector({ data: rawData, type }) : null;
  type WithId = { id: string } & { [otherKey in KeyType]: any };
  const D = Vue.extend({
    data() {
      return {
        command,
        dc_: data,
      };
    },
    computed: {
      type() {
        if (!this.dc_) return null;
        return this.dc_.type;
      },
      data() {
        if (!this.dc_) return null;
        return this.dc_.data;
      },
      datum() {
        if (!this.dc_) return null;
        return this.dc_.datum;
      },
      dataArr() {
        if (!this.dc_) return null;
        return this.dc_.dataArr;
      },
    },
    methods: {
      set({ command: _command, ...obj }:
      { command ?: string, data: any, type: string, [otherKey: string]: any }) {
        if (obj.data) {
          if (_command) {
            this.command = _command;
          }
          if (!this.dc_ || this.dc_.data !== obj.data) {
            const dc = new DataCollector(obj);
            mutatate({ command: this.command, data: dc.data });
            this.dc_ = dc;
          }
        } else {
          this.clear();
        }
      },
      clear() {
        this.dc_ = null;
        clear();
      },
      contains(item: WithId) {
        if (!this.dc_) return false;
        return this.dc_.dataArr.some((_item: any) => _item.id === item.id);
      },
      toObject() {
        if (!this.dc_) return {};
        return this.dc_.dataArr.reduce((memo, item) => {
          if (item && item.id) {
            memo[item.id] = item;
          }
          return memo;
        }, {} as {[x: string]: WithId});
      },
      toJSON() {
        if (this.dc_) {
          return { type: this.dc_.type, data: this.dc_.data, command: this.command };
        }
        return null;
      },
      get() {
        if (this.dc_) {
          return Object.create(this.dc_, {
            command: {
              value: this.command,
            },
          });
        }
        return null;
      },
    },
  });
  const result = new D();
  return result;
}

type KeyType = string | number;

const a = parseData(() => {
  const d = localStorage.getItem(storageKey);
  if (!d) {
    return null;
  }
  return JSON.parse(d);
}, ({ data, command }) => {
  localStorage.setItem(storageKey, JSON.stringify({ data, command }));
}, () => {
  localStorage.removeItem(storageKey);
});

