import { defineComponent, ref } from '@vue/composition-api';
import Jsonschema from './JSONschema';

function generateDefualtOptions() {
  return ['aaa', 'bbb', '3'].map(option => (
    new Jsonschema({
      type: 'string',
      const: option as 'string',
    })
  ));
}

const selectType = defineComponent({
  name: 'SingleSeletor',
  props: {
    builder: {
      type: String,
      default: 'el-select',
    },
    subbuilder: {
      type: String,
      default: 'el-option',
    },
    options: {
      type: Array as () => Array<Jsonschema>,
      default: generateDefualtOptions(),
    },
    val: {
      type: [String, Number],
      default: '',
    },
    valueKey: {
      type: String,
      default: '',
    },
    labelKey: {
      type: String,
      default: '',
    },
  },
  model: {
    prop: 'val',
    event: 'change',
  },
  render() {
    const s = this as InstanceType<typeof selectType>;
    const { options, builder, subbuilder } = s;
    const isObject = typeof options[0].const === 'object';
    return (
      <builder value={s.val} onchange={(n: string) => { s.$emit('change', n); }}
      {...{ attrs: s.$attrs }}>
        {options.map((op) => <subbuilder
        value={isObject ? op.const![s.valueKey!] : op.const}
        key={isObject ? op.const![s.valueKey!] : op.const}
        label={isObject ? op.const![s.labelKey!] : op.const} />)}
      </builder>
    );
  },
});

export default selectType;

const MultipleSeleteType = defineComponent({
  name: 'MultipleSeletor',
  props: {
    val: {
      type: Array as () => Array<number | string>,
      default: () => [],
    },
    builder: {
      type: String,
      default: 'el-select',
    },
    subbuilder: {
      type: String,
      default: 'el-option',
    },
    options: {
      type: Array as () => Array<Jsonschema>,
      default: () => generateDefualtOptions(),
    },
  },
  model: {
    prop: 'val',
    event: 'change',
  },
  render() {
    const s = this as InstanceType<typeof MultipleSeleteType>;
    const { options, builder, subbuilder } = s;
    return (
      <builder multiple value={s.val} onchange={(v: string[]) => s.$emit('change', v)}>
        {options.map((op, index) => <subbuilder value={op.const} key={index} label={op.const} />)}
      </builder>
    );
  },
});

export { MultipleSeleteType };
