import {
  computed, defineComponent, ref, getCurrentInstance, watch,
} from '@vue/composition-api';
import { ElForm } from 'element-ui/types/form';
import Vue, { VNode } from 'vue';
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import validateForm from '@/utils/validators/validate-form';
import isFilled from '@/utils/validators/is-filled';
import Jsonschema from './JSONschema';
// import ObjectType from './ObjectType';
import StringType from './StringType';
import NumberType from './NumberType';
import IntegerType from './IntegerType';
import SelectType, { MultipleSeleteType } from './SelectType';

type DynamicFormItems = {
  name: string,
  value: any,
};

export type ResolveFunc = ((cur: Boolean, resolve: (val: boolean) => boolean)=> boolean);

type VariablesInjector = {
  [key: string]: ResolveFunc | VariablesInjector
};

// const obs = Vue.observable({ t: [] as Jsonschema[][] });

export function generateRule(target: Jsonschema, fns?: {
  [key: string]: Function
}) {
  const validateFunctions:Function[] = [];
  if (target.required) {
    validateFunctions.push((v: string) => isFilled(v, { message: '不能为空' }));
  }

  if (target.type === 'number') {
    // number相关校验
    if (target.maximum !== undefined) {
      validateFunctions.push((value: number | string | null) => {
        if (value === null) return;
        if (+value > target.maximum!) throw new Error(`最大不得超过${target.maximum}`);
      });
    }
    if (target.minimum !== undefined) {
      validateFunctions.push((value: number | string | null) => {
        if (value === null || +value < target.minimum!) throw new Error(`最小不得小于${target.minimum}`);
      });
    }
    if (target.exclusiveMaximum !== undefined) {
      validateFunctions.push((value: number | string | null) => {
        if (value === null) return;
        if (+value >= target.exclusiveMaximum!) throw new Error(`最大不得超过${target.exclusiveMaximum}`);
      });
    }
    if (target.exclusiveMinimum !== undefined) {
      validateFunctions.push((value: number | string | null) => {
        if (value === null || +value <= target.exclusiveMinimum!) throw new Error(`最小不得小于${target.exclusiveMinimum}`);
      });
    }
  }

  if (target.type === 'array') {
    // array相关校验
    if (target.minItems !== undefined) {
      validateFunctions.push((value: Array<any>) => {
        if (value.length < target.minItems!) throw new Error(`至少需要${target.minItems}个单元`);
      });
    }
  }

  if (target.type === 'string') {
    // string相关校验
    if (target.minLength !== undefined) {
      validateFunctions.push((value: string | null) => {
        if (value === null || value.length < target.minLength!) throw new Error(`最少不得少于${target.minLength}个字符`);
      });
    }
    if (target.maxLength !== undefined) {
      validateFunctions.push((value: string | null) => {
        if (value === null) {
          return;
        }
        if (value.length > target.maxLength!) throw new Error(`最多不得超过${target.maxLength}个字符`);
      });
    }
  }

  if (fns && target.validator && target.validator.length) {
    target.validator.forEach(fnName => {
      const t = fns[fnName];
      if (t) validateFunctions.push(t);
    });
  }

  const a = validator2ElemRule((value: any) => {
    validateFunctions.forEach(f => f(value));
  });
  return a;
}

function findVariablesObject(
  targetJSONschema: Jsonschema,
  variablesObject: VariablesInjector,
) {
  const keys = Object.keys(variablesObject) as (keyof Jsonschema)[];
  const targetKey = keys.find(key => key === targetJSONschema.$id);
  if (!targetKey) throw new Error('未找到对应$id，请确认formItemVariablesInjector的key是否和对应JSONschema的$id一致');
  return variablesObject[targetKey] as VariablesInjector;
}

function getUpdatableVariables(
  // eslint-disable-next-line no-use-before-define
  this: InstanceType<typeof schema2Type>,
  targetJSONschema: Jsonschema,
) {
  if (!targetJSONschema.$id) throw new Error(`jsonschema的${targetJSONschema}缺乏$id属性`);
  if (!this.calculateVariables[targetJSONschema.$id]) {
    this.$set(this.calculateVariables, targetJSONschema.$id, {});
    const keys = targetJSONschema.needCalculateItems!;
    keys.forEach(key => this.$set(this.calculateVariables[targetJSONschema.$id!], key, {
      value: false,
      done: false,
    }));
  }
  const variablesObject = this.calculateVariables[targetJSONschema.$id];
  const keys = Object.keys(variablesObject).filter(k => !variablesObject[k]!.done);
  return { keys, variablesObject };
}

function findAndInjectVariables(
  // eslint-disable-next-line no-use-before-define
  this: InstanceType<typeof schema2Type>,
  targetJSONschema: Jsonschema,
  variablesObject: VariablesInjector,
) {
  const { keys: updatableKeys, variablesObject: curViariesObject } = getUpdatableVariables.call(this, targetJSONschema);
  if (!updatableKeys.length) return;
  const obj = findVariablesObject(targetJSONschema, variablesObject);
  updatableKeys.forEach(key => {
    if (!obj[key]) throw new Error(`formItemVariablesInjector中的${key}函数不合法。没有找到此函数`);
    // eslint-disable-next-line no-new-func
    const c = new Function(`return ${String(obj[key] as ResolveFunc)}`) as () => ResolveFunc;
    const res = c().call(this, this, curViariesObject[key].value, (v: boolean) => {
      curViariesObject[key].done = true;
      return v;
    }, key);
    console.log(res);
  });
}

function generateFormItem(
  // eslint-disable-next-line no-use-before-define
  this: InstanceType<typeof schema2Type>,
  key: string,
  target: Jsonschema,
  form: {
    [k: string]: any,
  },
  required = false,
  h = this.$createElement,
  ...args: any[]
) {
  // if (target.needCalculateItems && target.needCalculateItems.length && this.formItemVariablesInjector) {
  //   findAndInjectVariables.call(this, target, this.formItemVariablesInjector);
  // }
  // jsx用到了h
  h;
  const on = (_key: string) => {
    return {
      change: (value: any) => this.$emit('change', { key: _key, value }),
      input: (value: any) => this.$emit('input', { key: _key, value }),
    };
  };
  const { type } = target;
  const { wrapperClass, wrapperStyle } = target;
  let _class: string[] = [];
  const className = this.settings.formItemClass;
  if (typeof wrapperClass === 'string') {
    if (wrapperClass !== this.settings.formItemClass) { _class = [wrapperClass, className]; }
  } else if (typeof wrapperClass === 'object') {
    if (!wrapperClass.includes(this.settings.formItemClass)) { _class = [...wrapperClass, className]; }
  } else {
    _class = [className];
  }
  const label = target[this.settings.formItemTitle as 'title'];
  const formItem = <el-form-item prop={key} label={label} key={key} class={_class}
  style={wrapperStyle}
  required={required} rules={generateRule(target, this.formItemValidator)}></el-form-item>;
  const klass = target.class;
  const { style } = target;
  let { show } = target;
  if (
    this.formItemVariablesInjector
    && this.formItemVariablesInjector[target.$id!]
    && (this.formItemVariablesInjector[target.$id!] as any).show
  ) {
    // eslint-disable-next-line prefer-destructuring
    show = (this.formItemVariablesInjector[target.$id!] as any).show;
  }
  const {
    $id: _, type: __, [this.settings.formItemTitle as 'title']: ___, ...attrs
  } = target;
  let slot: VNode | undefined;
  if (this.formItemGenerator) {
    slot = this.formItemGenerator.call(this, target, form, key, attrs);
    if (slot) {
      if (slot.data && klass) {
        slot.data.class = klass;
      }
      if (slot.data && style) {
        slot.data.style = style;
      }
      // if (slot.componentOptions && slot.componentOptions.propsData && disabled) {
      //   slot.componentOptions.propsData = {
      //     ...slot.componentOptions.propsData,
      //     disabled,
      //   } as any;
      // }
      formItem.componentOptions!.children = [
        slot,
      ];
      return formItem;
    }
  }
  switch (type) {
    case 'object':
      // eslint-disable-next-line no-case-declarations
      const _slot = <schema2Type ref={key} jsonschema={target} form={form[key]}
      formItemGenerator={this.formItemGenerator}
      formItemValidator={this.formItemValidator}
      formItemVariablesInjector={this.formItemVariablesInjector}
      on={{
        change: (value: any) => this.$emit('change', value),
        input: (value: any) => this.$emit('input', value),
      }} {...{ attrs: this.$attrs }} />;
      formItem.componentOptions!.children = [
        _slot,
      ]; break;
    case 'number': formItem.componentOptions!.children = [
      <NumberType on={on(key)} {...{ attrs }} vModel={form[key]}/>,
    ]; break;
    case 'integer': formItem.componentOptions!.children = [
      <IntegerType on={on(key)} {...{ attrs }} vModel={form[key]} />,
    ]; break;
    case 'array': formItem.componentOptions!.children = [
      <MultipleSeleteType on={on(key)} {...{ attrs }} vModel={form[key]} />,
    ]; break;
    case 'string': if (target.oneOf) {
      formItem.componentOptions!.children = [
        <SelectType {...{ attrs }} on={on(key)}
        vModel={form[key]} options={target.oneOf} />,
      ];
      break;
    }
    // eslint-disable-next-line no-fallthrough
    default:
    // eslint-disable-next-line
    formItem.componentOptions!.children = [
      <StringType on={on(key)} {...{ attrs }} type={target.inputType || 'text'} vModel={form[key]} />,
    ];
  }
  const { data } = formItem.componentOptions!.children[0];
  if (data) {
    if (klass) data.class = klass;
    if (style) data.style = style;
  }
  const wrap = target.notNeedFormItem ? data : formItem.data;
  if (wrap && typeof show === 'boolean') {
    wrap.directives = [
      ...(wrap.directives || []),
      {
        name: 'show',
        value: show,
      },
    ];
  }
  if (target.notNeedFormItem) return formItem.componentOptions!.children[0];
  return formItem;
}

const setting = {
  formItemClass: 'form-item',
  formItemTitle: 'title',
};

const schema2Type = defineComponent({
  name: 'Schema2Type',
  props: {
    jsonschemaString: {
      type: String,
    },
    jsonschema: {
      type: Object as () => Jsonschema,
    },
    /** 表单的formData */
    form: {
      type: Object,
      default: () => ({}),
    },
    /** 支持传入一个函数，用于解析自定义字段并使用第三方组件 */
    formItemGenerator: {
      type: Function as unknown as () => ((i: Jsonschema, form: {
        [key: string]: any
      }, key: string, attrs: Partial<Jsonschema>)=>(VNode | undefined)),
    },
    /** 支持传入带有名称的校验函数，用在使用了此名称的自定义校验项中 */
    formItemValidator: {
      type: Object as () => ({
        [key: string]: Function
      }),
    },
    formItemVariablesInjector: {
      type: Object as () => VariablesInjector,
    },
    settings: {
      type: Object as () => typeof setting,
      default: () => setting,
    },
    showValidateButton: { type: Boolean, default: false },
  },
  render() {
    const contentGenerator = (shouldSort: boolean) => {
      const properties = this.computedJSONschemas.properties!;
      if (shouldSort) {
        const { sort } = this.computedJSONschemas;
        try {
          if (!sort || !sort.length) throw new Error('sort读取失败，请确保sort是字符串数组，且长度不为零');
          const content = sort.map(key => {
            const target = properties[key];
            return generateFormItem.call(
              this, key, target, this.form, !!this.computedJSONschemas.required?.find(k => k === key),
            );
          });
          return content;
        } catch (e) {
          throw new Error(e.message);
        }
      } else {
        const content = Object.keys(this.form).map(key => {
          const target = properties[key];
          return generateFormItem.call(
            this, key, target, this.form, !!this.computedJSONschemas.required?.find(k => k === key),
          );
        });
        return content;
      }
    };
    const s = this as InstanceType<typeof schema2Type>;
    const shouldSort = !!s.computedJSONschemas.sort;
    const a = <el-form props={{ model: s.form }} ref='formRef' {...{ attrs: s.$attrs }}>
      {
        contentGenerator(shouldSort)
      }
      {
        <el-button vShow={s.showValidateButton} type='primary' onclick={s.validate}>校验</el-button>
      }
    </el-form>;
    return a;
  },
  setup(props) {
    const current = getCurrentInstance()! as InstanceType<typeof schema2Type>;
    const computedJSONschemas = computed(() => {
      if (!props.jsonschema && !props.jsonschemaString) {
        return new Jsonschema({ type: 'object', properties: {} });
      }
      try {
        if (props.jsonschema) {
          return props.jsonschema;
        }
        const schema = new Jsonschema(
          JSON.parse(props.jsonschemaString!) || { type: 'object', properties: {} },
        );
        // console.log(schema);
        return schema;
      } catch (e) {
        return new Jsonschema({ type: 'object', properties: {} });
      }
    });
    const subForms:Array<String> = [];
    watch(() => computedJSONschemas.value.properties, (newProperties, oldProperties) => {
      if (oldProperties) {
        const keys = Object.keys(oldProperties);
        keys.forEach(key => {
          const { type } = oldProperties[key];
          if (typeof type === 'object') {
            subForms.splice(subForms.indexOf(key), 1);
            if (current.$refs[key]) current.$delete(current.$refs, key);
          }
          current.$delete(current.form, key);
        });
      }
      if (newProperties) {
        const keys = Object.keys(newProperties);
        keys.forEach(key => {
          if (current.form[key] !== undefined) {
            current.$set(current.form, key, current.form[key]);
            return;
          }
          const { type } = newProperties[key];
          if (type === 'object') {
            current.$set(current.form, key, {});
            subForms.push(key);
          } else if (type === 'array') {
            current.$set(current.form, key, []);
          } else if (type === 'number' || type === 'integer') {
            current.$set(current.form, key, null);
          } else current.$set(current.form, key, null);
        });
      }
    }, {
      immediate: true,
    });
    const formRef = ref<ElForm>();
    async function validate() {
      console.log('开始校验');
      await Promise.all([
        validateForm(current.formRef!),
        subForms.map(subForm => { return validateForm(current.$refs[subForm] as ElForm); }),
      ]);
      console.log('结束校验');
      return true;
    }

    return {
      computedJSONschemas,
      formRef,
      calculateVariables: ref<{[key: string]: {[k2: string]: {
        value: boolean, done: boolean
      }}}>({}),

      validate,
    };
  },
});

export default schema2Type;
