import { defineComponent, ref } from '@vue/composition-api';

const stringType = defineComponent({
  props: {
    builder: {
      type: String,
      default: 'el-input',
    },
    val: {
      type: String,
      default: '',
    },
    // disabled: {
    //   type: Boolean,
    //   default: false,
    // },
    type: {
      type: String as () => 'text' | 'password' | 'email' | 'textarea' | 'color' | 'url',
      default: () => 'text',
    },
  },
  model: {
    prop: 'val',
    event: 'input',
  },
  render() {
    const s = this as InstanceType<typeof stringType>;
    const { builder } = s;
    return <builder props={{
      ...this.$props,
      value: s.val,
    }} oninput={(v: string) => {
      s.$emit('input', v);
    }} onchange={(v: string) => {
      s.$emit('change', v);
    }} {...{ attrs: s.$attrs }}></builder>;
  },
});

export default stringType;
