


























import {
  defineComponent, ref, computed, onMounted, provide, toRefs,
} from '@vue/composition-api';

import axios from '@/utils/axios';
import { blockDragEffect } from '@/utils/public';
import { getContextUser } from '@/loaders/context';
import { getCurrentCompany } from '@/loaders/company';

import useLoader from '_/composables/use-loader';
import extract from '_/extract';

import Form from './Form.vue';
import ModelsSelector from './ModelsSelector.vue';
import AttachmentImage from './AttachmentImage.vue';
import AttachmentVideo from './AttachmentVideo.vue';

/**
 * 更新材质详情信息（标签更新在其他接口）
 * @param {String} data { id: string, propName: any }
 * @see {Promise}
 */
export const updateMaterialInfo = (data: any) => {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/updateNewMaterialInfo',
    data,
  });
};

// 获取中类categoryId
async function fetchMiddleCategoryId(id: string) {
  const ids = (await axios({
    method: 'GET',
    url: '/anta-material/category/parents',
    params: { id },
  })).data as string[];
  return ids[1];
}

// 判断材质修改提醒
async function fetchShowAntaNote(originalId: string) {
  return (await axios({
    method: 'GET',
    url: '/show/material/relate/needAlert',
    params: { originalId },
  })).data;
}

type MaterialSelectItem = {
  id: string,
  name: string,
  subCount?: number,
  children?: MaterialSelectItem[]
  parentIds?: string[]
  leaf?: boolean
};

export default defineComponent({
  name: 'MaterialDetailForm',
  components: {
    Form,
    ModelsSelector,
    AttachmentImage,
    AttachmentVideo,
  },
  props: {
    material: {
      type: Object,
      required: true,
      default: () => ({}),
    },
    entryAppType: {
      type: Number,
    },
    groupAppId: {
      type: String,
    },
    // middleCategoryId: {
    //   type: String,
    //   required: true,
    // },
  },

  setup(props, {
    // @ts-ignore
    emit, refs, root: { $errHandler, $message },
  }) {
    // 创建者
    const isCreator = computed(() => getContextUser()?.id === props.material.creator);

    provide('getIsCreator', isCreator);

    // 中类ID
    const middleCategoryId = useLoader(async ({ material: { extraProperty: { category } } }) => {
      // eslint-disable-next-line prefer-rest-params
      console.info(category);
      if (!category) return '';
      return fetchMiddleCategoryId(category);
    }, extract(toRefs(props), ['material']), {
      initialValue: null,
      loadingValue: () => null,
    });

    provide('getMiddleCategoryId', middleCategoryId);

    const materialCopy = ref(null);

    const newMaterial = ref({} as { [key: string]: any });

    onMounted(async () => {
      // 禁止浏览器拖拽的默认行为
      blockDragEffect(refs.uploadArea);

      // 防止修改未保存时影响详情的数据
      materialCopy.value = JSON.parse(JSON.stringify(props.material));
      // console.info(props.material);
      // console.info(JSON.parse(JSON.stringify(props.material)));
      // console.info(materialCopy.value);
    });

    // 保存
    async function save() {
      // emit('loading', true);


      newMaterial.value = await refs.refsForm.getFormData();

      console.info(newMaterial.value);
      if (!newMaterial.value) return;

      try {
        // console.info(updateObj.value);
        newMaterial.value.id = props.material.id;
        newMaterial.value.assetsInfo = {}; // 初始assetsInfo接口有时返回null，访问 models, images, videos会报错

        // 获取模型选择的结果
        const models = await refs.modelsSelector.getSelectResult();
        newMaterial.value.assetsInfo.models = models.map((model: any) => {
          return { id: model.id, cover: model.modelCover };
        });

        // 获取材质图片的结果
        const images = await refs.attachmentImage.getImageOssPaths();
        newMaterial.value.assetsInfo.images = images;

        // 获取模型选择的结果
        const videos = await refs.attachmentVideo.getVideoOssPaths();
        newMaterial.value.assetsInfo.videos = videos;
        newMaterial.value.businessType = 1;

        // newMaterial.value.extraProperty = props.material.extraProperty;

        // newMaterial.value.extraProperty.material_status = 1;

        // return;
        try {
          await updateMaterialInfo(newMaterial.value);
          await refs.refsForm.addOperateToRecord(props.groupAppId);
        } catch (e) {
          $errHandler(e);
        }
        newMaterial.value = {};
        emit('switch-component', 'ComponentPreviewDetail');
        // emit('update:saveDisabled', true);
        emit('refresh');
      } catch (err) {
        $errHandler(err);
      } finally {
        emit('loading', false);
      }
    }

    return {
      save,
      isCreator,
      materialCopy,
      middleCategoryId,
    };
  },
});
