





















import {
  defineComponent, toRefs, watch, onBeforeMount, markRaw, shallowReactive,
} from '@vue/composition-api';
import useClosest from '_/composables/use-closest';
import {
  getCombines, Options, Value, ObjValue,
} from './utils';


let allOptions:Options = [];
let prevOptions:Options = [];


const getSliceList = (lableStr:string = '', limit = 100) => {
  const filters = allOptions.filter(item => item.value.includes(lableStr));
  // const yearStr = lableStr.slice(0, 2);
  // const seasonStr = lableStr.slice(2,4);
  // const productTypeStr = lableStr.slice(4);
  const slices = filters.slice(0, limit);
  return slices;
};


export default defineComponent({
  name: 'season-select',
  props: {
    value: {
      type: Object as ()=>Value,
      requied: true,
    },
    productTypes: {
      type: Array,
    },
  },
  setup(props, { emit }) {
    const ElFormItem = useClosest('ElFormItem');
    const emptyValue = null;
    const state = shallowReactive({
      currentValue: emptyValue as string | null,
      options: markRaw([]) as Options,
    });

    const value2CurrenValue = (val?:Value) => {
      const stringifyValue = (objVal:ObjValue) => {
        const obj = {
          year: objVal.year,
          season: objVal.season,
          品牌: objVal.品牌,
        };
        return JSON.stringify(obj);
      };
      state.currentValue = !val ? emptyValue : stringifyValue(val!);
    };

    const currentValue2Value = (val:string|null) => {
      const value = !val ? emptyValue : JSON.parse(val);
      emit('input', value);

      // 解决校验时拿的是旧值的问题
      return ElFormItem.value?.$emit('el.form.change');
    };

    const init = async () => {
      allOptions = await getCombines(props.productTypes);
      // 初始加载时的options加载筛选逻辑，如果有值，默认以年份搜索
      const initLableStr = props.value?.year?.slice(2, 4) || '';
      state.options = getSliceList(initLableStr);
      value2CurrenValue(props.value);
    };

    watch(() => props.value, val => {
      value2CurrenValue(val);
    });

    watch(() => state.currentValue, val => {
      currentValue2Value(val);
    });

    watch(() => props.productTypes, () => {
      init();
    }, {
      immediate: true,
    });


    // onBeforeMount(() => {
    //  init();
    // });

    const handlerFilter = (value:string) => {
      state.options = getSliceList(value);
    };

    const handleChange = () => {
      prevOptions = state.options;
    };

    const handleVisable = (isShow:boolean) => {
      if (isShow) return;
      if (!prevOptions?.length) return;
      state.options = prevOptions;
    };

    return {
      ...toRefs(state),
      handlerFilter,
      handleChange,
      handleVisable,
    };
  },
});
