import {
  defineComponent, inject, onMounted, watch, toRef,
} from '@vue/composition-api';
// import useClosest from '_/composables/use-closest';

import NumberType from '@/components/jsonschema2form/NumberType';
// import DictionarySelectorNew from '@/components/dictionary/DictionarySelectorNew.vue';
// import rules from '../detail-validate';
import { getContextUser } from '@/loaders/context';

import isFilled from '@/utils/validators/is-filled';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';

type ReferencePrice = {
  reference_price_value: number,
  reference_price_unit: number,
  supplier_name: string,
  cost_value: number,
  cost_unit: number,
  supplier_status: boolean,
};

export default defineComponent({

  props: {
    item: {
      type: Object as () => Partial<ReferencePrice>,
      require: true,
      default: () => ({
        reference_price_value: 0,
        reference_price_unit: 1,
        supplier_name: '',
        cost_value: '',
        cost_unit: 1,
        supplier_status: 1,
        delete_status: 1,
      }),
    },
    disabled: Boolean,
    index: Number,
    isSupplier: {
      type: Boolean,
      default: false,
    },
    supplierList: {
      type: Object,
      default: null,
    },
  },

  model: {
    prop: 'item',
    event: 'change',
  },

  setup(props, { emit }) {
    // const aaa = computed({ get: () => props.supplierList, set: (val) => Object.keys(val) || [] });
    // const aaa = toRef(props, 'supplierList');
    // watch(() => props.supplierList, (val) => {
    //   console.info(val);
    //   JSON.stringify(val);
    //   console.info(JSON.stringify(val));
    //   console.info(Object.keys(val));
    //   aaa.value = Object.keys(val);
    //   console.info(aaa.value);
    // }, {
    //   immediate: true,
    // });

    const status = inject('getStatus');

    console.info(status);

    onMounted(() => {
      if (status === 'upload' && props.index === 0) {
        const companyId = getContextUser()?.companyId;
        console.info(companyId);
        console.info(props.supplierList);
        props.item.supplier_name = Object.values(props.supplierList).find((sup: {companyId: string}) => sup.companyId === companyId)?.id || '';
        emit('change', props.item);
      }
    });

    return {
      status,
    };
  },

  render() {
    const {
      status, supplierList, disabled, isSupplier, index,
    } = this;
    // console.info(Object.keys(supplierList));
    return <div class="flex-wrap width-100">
      <el-form-item label="供应商名称" prop={`extraProperty.supplier_price_array.${index}.supplier_name`} size="small"
        required
        rules={ validator2ElemRule(isFilled) }>
      {supplierList
        ? <el-select vModel={this.item.supplier_name} style="width:100%" disabled={ status === 'edit' || isSupplier }>
          {
          Object.keys(supplierList).map(supId => {
            return <el-option value={supId} label={supplierList[supId].name || supplierList[supId].originalSupplierName}></el-option>;
          })
          }
        </el-select> : null
      }
      </el-form-item>
      <el-form-item label="参考价格" rules={ validator2ElemRule((val: string|number) => { if (!val) throw new Error('不能为空'); }) }
        required
        prop={`extraProperty.supplier_price_array.${index}.reference_price_value`}>
        <div class="flex" style="width: 100%;">
          <NumberType
            disabled={ disabled }
            size="small"
            toFixed={3}
            vModel={this.item.reference_price_value}
          />
          <el-select
            disabled={disabled}
            vModel={this.item.reference_price_unit}
            type="priceUnit"
            size="small"
            style="width: 124px; margin-left: 6px"
            >
            <el-option value={1} label='人民币'></el-option>
          </el-select>
        </div>
      </el-form-item>
      {/* {
        status !== 'upload' ? <el-form-item style="width: 400px;" label="成本价">
          <div class="flex" style="width: 100%;">
            <NumberType
              toFixed={3}
              vModel={this.item.cost_value}
            />
            <DictionarySelectorNew
              vModel={this.item.cost_unit}
              type="priceUnit"
              style="width: 124px; margin-left: 6px"
            />
          </div>
        </el-form-item> : null
      } */}
      {/* <el-form-item vShow={status === 'edit'} style="width: 400px;" label="是否默认供应商">
        <el-checkbox style="width: 100%;"
          vModel={ this.item.supplier_status }>
          是
        </el-checkbox>
      </el-form-item> */}
    </div>;
  },

});
