import Jsonschema from '@/components/jsonschema2form/JSONschema';
import axios from '@/utils/axios';

export type IAttribute = {
  id?: string,
  title: string,
  required: boolean,
  values?: IOption[] | undefined,
  component: 1 | 2 /** 小数、整数、文本是1，下拉类型是2 */,
  params?: Partial<Jsonschema>,
};

export type IOption = {
  id?: string,
  label: string,
  sort: number,
};

export function attributeValueExtractFEId(list: IAttribute['values']) {
  if (!list) return list;
  return list.map((item) => {
    const { id, label, sort } = item;
    if (id?.startsWith('@FE')) {
      return {
        label, sort,
      };
    }
    return { id, label, sort };
  });
}

export function attribute2JSONschema(itemOrList: IAttribute | IAttribute[]) {
  const list = (
    Object.prototype.toString.call(itemOrList) === '[object Array]' ? itemOrList : [itemOrList]
  ) as IAttribute[];
  const JSONschemaList = list.map(attr => {
    if (!attr.params) {
      attr.params = new Jsonschema({ type: 'string' });
    }
    // 增加title
    attr.params = {
      ...attr.params,
      title: attr.title,
    };
    if (attr.required) attr.params.required = [];
    switch (attr.component) {
      case 1: return { ...attr.params! } as Jsonschema;
      case 2: {
        const oneOf = attr.values!.map(av => new Jsonschema({
          type: 'string',
          const: {
            label: av.label,
            value: av.id!,
          } as unknown as 'object',
          $id: av.id!,
        }));
        attr.params = {
          ...attr.params,
          oneOf,
          valueKey: 'value',
          labelKey: 'label',
          validator: [attr.title],
        };
        return ({ ...attr.params!, oneOf }) as Jsonschema;
      }
      default: return new Jsonschema({ type: 'string' });
    }
  });
  return list.length > 1 ? JSONschemaList : JSONschemaList[0];
}

/** 将输入的JSONschema数组包成一个JSONschema */
export function JSONschemaList2JSONschema(list: Jsonschema[], useSort = true): Jsonschema {
  const wrapper = new Jsonschema({ type: 'object', properties: {} });
  wrapper.properties = {
    ...list.reduce((pre, cur) => ({
      ...pre,
      [cur.title!]: cur,
    }), {}),
  };
  wrapper.required = [...(list.filter(item => item.required).map(item => item.title) as string[])];
  if (useSort) {
    wrapper.sort = list.map(item => item.title!);
  }
  return wrapper;
}

/** 输入中类id和formData，返回转换好的formData */
export async function antaMiddleClassIdAndFormData2TransformedFormDataList(
  middleClassId: string,
  formData: {[key: string]: string | number},
) {
  const properties = (await axios({
    url: '/anta-material/template/middle',
    params: { id: middleClassId },
    method: 'GET',
  })).data as { propertyId: string, sort: number }[];
  if (!properties.length) return formData;
  const pIds = properties.sort(
    (a, b) => Math.abs(a.sort) - Math.abs(b.sort),
  ).map(property => property.propertyId);

  const list = await Promise.all(pIds.map(async (id) => {
    return (await axios({
      url: `/anta-material/dynamics/property/detail?id=${id}`, method: 'GET',
    })).data as IAttribute;
  }));

  const l = await Promise.all(list.map(async (item) => {
    switch (item.component) {
      case 1: return { label: item.title, value: formData[item.title] };
      case 2: {
        // 先判断是否在可选值中。如果在可选值中，直接获取可选值，否则还要根据id调接口获得
        const wait4FetchValue = formData[item.title] as string;
        const target = item.values!.find(i => i.id === wait4FetchValue);
        if (target) return { label: item.title, value: target.label };
        const label = (await axios({
          url: '/anta-material/dynamics/property/value/id',
          method: 'GET',
          params: { id: wait4FetchValue },
        })).data as string;
        return { label: item.title, value: label };
      }
      default: throw new Error(`数据转换出错。当前component为${item.component}`);
    }
  }));
  return l;
}

/** 输入中类id和formData，返回转换好的formData */
export async function antaMiddleClassIdAndFormData2TransformedFormData(
  middleClassId: string,
  formData: {[key: string]: string | number},
) {
  const properties = (await axios({
    url: '/anta-material/template/middle',
    params: { id: middleClassId },
    method: 'GET',
  })).data as { propertyId: string }[];
  if (!properties.length) return formData;
  const pIds = properties.map(property => property.propertyId);

  const list = await Promise.all(pIds.map(async (id) => {
    return (await axios({
      url: `/anta-material/dynamics/property/detail?id=${id}`, method: 'GET',
    })).data as IAttribute;
  }));
  const res = { ...formData };
  const filterList = list.filter(attr => attr.component === 2);
  const shouldFetchList:{key: string, id: string}[] = [];
  filterList.forEach(item => {
    const key = item.title;
    const wait4FetchValue = formData[key] as string;
    const target = item.values!.find(i => i.id === wait4FetchValue);
    // 找到了
    if (target) res[key] = target.label;
    else shouldFetchList.push({ key, id: wait4FetchValue });
  });
  if (shouldFetchList.length) {
    await Promise.all(shouldFetchList.map(async ({ id, key }) => {
      const value = (await axios({
        url: '/anta-material/dynamics/property/value/id',
        method: 'GET',
        params: { id },
      })).data as string;
      console.log(value);
      res[key] = value;
    }));
  }
  return res;
}

/** 输入jsonschema，返回包含检验下拉框值的validator函数组成的对象 */
export function jsonschema2FormItemValidatorObject(jss: Jsonschema) {
  if (jss.type !== 'object') throw new Error('输入的jsonschema不是object类型');
  if (!jss.properties) throw new Error('输入的jsonschema没有properties');
  const validatorObject:{[key: string]: Function} = {};
  const validatorKeys = Object.keys(jss.properties!).filter(key => jss.properties![key].oneOf);
  validatorKeys.forEach(key => {
    validatorObject[key] = (value: string) => {
      const valueList = (jss.properties![key].oneOf!.map(item => item.const! as unknown as {
        label: string, value: string,
      })).map(({ value: _v }) => _v);
      console.log(valueList);
      if (!valueList.includes(value)) throw new Error('此值已被删除');
    };
  });
  return validatorObject;
}
