
import { defineComponent } from '@vue/composition-api';
import { VueConstructor } from 'vue';
import axios from '@/utils/axios';
import tryParseJson from '_/try-parse-json';
import { sync } from '_/jsx-utils';
import useLoader from '_/composables/use-loader';
import { CatalogInfoMaterial } from '../../../../models';
import { Loading } from '../../../../utils/plugins/vue-loading';
import useSetting from '../../../../utils/composables/use-setting';
import draggingData from '../../../../utils/dragging-data';
import _MaterialList from './CollectList.vue';

const MaterialList = _MaterialList as unknown as VueConstructor;
enum StoreStatus {
  outofStock,
  onStock
}

type CollectionLibraryMaterial = {
  id: string,
  detail: string,
  click: number,
  storeStatus: StoreStatus,
  material: CatalogInfoMaterial
};
async function loadMaterialGoods() {
  const s: any[] = (await axios.get('mall/collect/list')).data;
  return s.map(({
    id, detail, click, storeStatus, materialName: name, materialId: originalId, imgJson, ...material
  }) => {
    return {
      id,
      detail,
      click,
      storeStatus,
      material: {
        // id,
        name,
        originalId,
        goodId: id,
        ...material,
        ...imgJson,
      },
    } as CollectionLibraryMaterial;
  });
}
export const LibraryList = defineComponent({
  setup(props) {
    const libarayMaterialGoods = useLoader(loadMaterialGoods, props, {
      loadingValue: (v) => (
        !v ? new Loading(v) : v),
    });
    window.addEventListener('storage', async e => {
      if (e.key === '__broadcast__' && e.newValue) {
        const broadcastEvent = tryParseJson(e.newValue);
        if (broadcastEvent.event === 'message') {
          const messageEvent = tryParseJson(broadcastEvent.data);
          if (messageEvent && messageEvent.constant
            && (messageEvent.constant.type === 'mall_collect_remove' || messageEvent.constant.type === 'mall_collect_add')) {
            libarayMaterialGoods.value = await loadMaterialGoods();
          }
        }
      }
    });

    const modelDetailViewType = useSetting('modelDetailViewType', 'drape');

    return {
      modelDetailViewType,
      libarayMaterialGoods,
    };
  },
  render() {
    const self = this as InstanceType<typeof LibraryList>;
    const { libarayMaterialGoods } = self;
    function updateMaterial(material: any) {
      // @ts-ignore
      self.$closest('model').updateMaterial(material);
    }

    if (!libarayMaterialGoods || Loading.isLoading(libarayMaterialGoods)) {
      return <div vLoading={true} style={{ height: '100px' }}/>;
    }
    const materials = Array.isArray(libarayMaterialGoods) ? libarayMaterialGoods.map(item => item.material) : [];
    // debugger;
    return <MaterialList
      class="material-list"
      materials={materials}
      onDrag={(e: DragEvent, data: any) => { draggingData.set({ type: 'material', data }); }}
      onPick={(material: any) => updateMaterial(material)}
      {...sync(self, { displayMode: 'modelDetailViewType' })}
    />;
  },
});
export default LibraryList;
